.containerContact {
  min-height: 100vh;
  width: auto;
  background: color #08071d;
  display: flex;
  justify-content: center;
  align-items: center;
  background: url("../assets/background.png") no-repeat center center/cover;
}

.containerContact form {
  width: 670px;
  height: 400px;
  display: flex;
  justify-content: center;
  box-shadow: 20px 20px 50px rgba(0, 0, 0, 0.5);
  border-radius: 15px;
  background: rgba(255, 255, 255, 0.1);
  backdrop-filter: blur(10px);
  flex-wrap: wrap;
}

.containerContact form h2 {
  color: black;
  font-weight: 500;
  margin-top: 20px;
  width: 500px;
  text-align: center;
}

.containerContact form input {
  width: 290px;
  height: 40px;
  padding-left: 10px;
  outline: none;
  border: none;
  font-size: 15px;
  margin-bottom: 10px;
  background: none;
  border-bottom: 2px solid #fff;
}
.containerContact form input::placeholder {
  color: black;
}

.containerContact form #email {
  margin-left: 20px;
}

.containerContact form h4 {
  color: black;
  font-weight: 300;
  width: 600px;
  margin-top: 20px;
}

.containerContact form textarea {
  background: none;
  border: none;
  border-bottom: 2px solid #fff;
  color: black;
  font-weight: 200;
  font-size: 15px;
  padding: 10px;
  outline: none;
  min-width: 600px;
  max-width: 600px;
  min-height: 80px;
  max-height: 80px;
}

textarea::-webkit-scrollbar-thumb {
  background-color: rgba(194, 194, 194, 0.713);
}
.containerContact form #button {
  border: none;
  color: #fff;
  background: #08071d;
  border-radius: 5px;
  margin-top: 5px;
  font-weight: 600;
  font-size: 20px;
  padding: 0;
}
